<template>
  <div @scroll="scroll">
    <Navbar></Navbar>
    <main class="main">
      <div class="container" >
        <div>
          <a  @click="nexPage(item.id)" @scroll="scroll(item)" :id="'id'+item.id"  @mousedown="getDelete($event)" class="market-item" v-for="(item,index) in markets.data" :key="index">
            <div>
              <div class="market-client">
                <div class="market-logo">
                  <img src="../../assets/images/png/Makro.png" alt="icon">
                </div>
                <div>
                  <div class="market-name">{{item.name}}</div>
                  <div class="market-price"><span class="market-back">
                <img src="../../assets/images/svg/back.svg" alt="icon"></span>
                    <div class="market-sum"><span>{{ item.market_balance }}</span><span> so'm</span>
                    </div>
                  </div>
                </div>
              </div>
              <div class="market-pin">
                <img src="../../assets/images/svg/pinned.svg" alt="icon">
              </div>
            </div>
          </a>
          <div class="face_top">
            <div class="empty-item" v-if="$store.state.pending==true">
              <div class="empty-logo"></div>
              <div>
                <div class="empty-name"></div>
                <div class="empty-price"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
    <Footer></Footer>
  </div>
</template>
<style>

</style>
<script>
import Navbar from "../../components/Header/Navbar";
import Footer from "../../components/Header/Footer";

import {mapGetters} from "vuex";
import axios from "axios";
export default {
  components: {Footer, Navbar},
  data(){
    return{
    item:20,
    markets:[]
    }
  },
  mounted() {
    axios.get("market?per-page="+this.item,{
      headers: {
        'Content-Type': 'application/json',
        'Authorization': 'Bearer ' + localStorage.getItem('token')
      }
    })
  .then((response) => {
   this.markets=response.data
  });
    this.scroll()
  },
  computed: {
    getMarket() {
      return this.$store.getters.allMagazine
    },
    ...mapGetters({
      user: "auth/user",
    }),
  },
  methods: {
    scroll() {

      window.onscroll = () => {

        let bottomOfWindow = document.documentElement.scrollTop + window.innerHeight === document.documentElement.offsetHeight;

        if (bottomOfWindow) {
          this.$store.state.pending=true
          this.item=this.item+20
            axios.get("market?per-page=" + this.item, {
              headers: {
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + localStorage.getItem('token')
              }
            })
            .then((response) => {
              this.markets=response.data
              this.$store.state.pending=false
            });
        }
      }
    },
    nexPage(item) {
      this.$router.push('/ru/shop/' + item)
    },
    getDelete(event) {
      if (event.type == 'mousedown') {
        setTimeout(() => {
              var reserved = document.getElementById(event.target.id);
              reserved.classList.add("active");
            }, 1000
        )
      }
    }
  }
}
</script>
<style>
.market-item{
  cursor: pointer;
}
.market-item.active{
  background: rgba(0, 55, 145, 0.05);
}
.empty-item{
  margin-top: 50px;
  margin-bottom: 100px;
}
.empty-item.active{
  margin-top: 0;
  margin-bottom: 100px;
}
.face_top{
  margin-bottom: 100px;
}
</style>
